@import url('https://fonts.googleapis.com/css?family=VT323');

body {
  margin: 0;
  padding: 0;
  font-family: 'VT323', monospace;
  font-size: 1.3em;
  color: #040F16;
}
.container{
  display: grid;
  grid-auto-columns: repeat(12, 1fr);
  grid-auto-rows: 11vh auto;
  grid-gap: 1vh;
}
.navbar{
  grid-column: 1 / span 12;
}
.current-video{
  grid-column: 1 / 11;
}
.sidebar-video-container{
  grid-column: 11 / 12;
  text-align: center;
}
.navbar{
  background: #001242;
  padding-top: 2vh;
  display: grid;
  grid-auto-columns: repeat(6, 1fr);
  grid-auto-rows: 1fr;
  grid-template-areas: ". b b b f f";
}
.navbar-brand{
  color: whitesmoke;
  font-size: 7vh;
  font-weight: 700;
  grid-area: b;
}
.form{
  grid-area: f;
}
.form-input{
  width: 70%;
  padding-top: 3vh;
  font-size: 3vh;
  font-family: 'VT323', monospace;
}
.form-button{
  font-family: 'VT323', monospace;
  background: whitesmoke;
  color: #040F16;
  border-radius: 100%;
  padding: 0.8em;
  font-size: 1em;
  outline: none;
}
.form-button:hover{
  background: firebrick;
  color: whitesmoke;
}
.form-input{
  background: transparent;
  border: none;
  border-bottom: solid whitesmoke 1px;
  color: whitesmoke;
  outline: none;
}
#iframe-wrapper {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
  margin-left: 1vh;
}
iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.recommended-videos{
  width: 100%;
  height: 100%;
}



